import { Pool, types } from "pg";

// ‡∏Å‡∏≥‡∏´‡∏ô‡∏î‡πÉ‡∏´‡πâ pg ‡πÑ‡∏°‡πà‡πÅ‡∏õ‡∏•‡∏á DATE ‡πÄ‡∏õ‡πá‡∏ô JavaScript Date object
// ‡πÄ‡∏û‡∏∑‡πà‡∏≠‡∏´‡∏•‡∏µ‡∏Å‡πÄ‡∏•‡∏µ‡πà‡∏¢‡∏á‡∏õ‡∏±‡∏ç‡∏´‡∏≤ timezone shift
types.setTypeParser(types.builtins.DATE, (val) => val); // DATE - return as string
types.setTypeParser(types.builtins.TIMESTAMP, (val) => val); // TIMESTAMP - return as string
types.setTypeParser(types.builtins.TIMESTAMPTZ, (val) => val); // TIMESTAMPTZ - return as string

// ‡∏™‡∏£‡πâ‡∏≤‡∏á connection pool ‡∏™‡∏≥‡∏´‡∏£‡∏±‡∏ö PostgreSQL (n8n.bjhbangkok.com)
const pool = new Pool({
  host: process.env.DB_HOST || "n8n.bjhbangkok.com",
  port: parseInt(process.env.DB_PORT || "5432"),
  user: process.env.DB_USER || "postgres",
  password: process.env.DB_PASSWORD || "Bjh12345!!",
  database: process.env.DB_NAME || "postgres",
  max: 20, // ‡∏à‡∏≥‡∏ô‡∏ß‡∏ô connection ‡∏™‡∏π‡∏á‡∏™‡∏∏‡∏î
  idleTimeoutMillis: 30000,
  connectionTimeoutMillis: 30000, // ‡πÄ‡∏û‡∏¥‡πà‡∏°‡πÄ‡∏õ‡πá‡∏ô 30 ‡∏ß‡∏¥‡∏ô‡∏≤‡∏ó‡∏µ
  statement_timeout: 60000, // Query timeout 60 ‡∏ß‡∏¥‡∏ô‡∏≤‡∏ó‡∏µ
  query_timeout: 60000,
  // n8n ‡πÑ‡∏°‡πà‡∏£‡∏≠‡∏á‡∏£‡∏±‡∏ö SSL
  ssl: false,
});
// ‡∏ï‡∏£‡∏ß‡∏à‡∏™‡∏≠‡∏ö‡∏Å‡∏≤‡∏£‡πÄ‡∏ä‡∏∑‡πà‡∏≠‡∏°‡∏ï‡πà‡∏≠
pool.on("connect", () => {
  // Connected to database
});
pool.on("error", (err) => {
  // Database error
  // ‡πÑ‡∏°‡πà exit ‡πÉ‡∏ô production ‡πÄ‡∏û‡∏∑‡πà‡∏≠‡πÉ‡∏´‡πâ retry ‡πÑ‡∏î‡πâ
  if (process.env.NODE_ENV !== "production") {
    process.exit(-1);
  }
});
export default pool;
